import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { DataService } from '../../core/services/data.service';

@Injectable({
  providedIn: 'root'
})
export class PaymentInfoService {

  constructor(protected dataService: DataService) { }

  getInitPaumentProcess(data: any): Observable<any> {
    return this.dataService.post<any>(`Payment/InitPaymentProcess`, data);
  }
  
  updateAcctBillingGroup(data:any):Observable<any>{
    return this.dataService.post<any>(`Payment/updateAcctBillingGroup`, data);
  }

}
