import { Component, Inject, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ActivatedRoute } from '@angular/router';
import { PaymentInfoService } from '../payment-info/payment-info.service';

@Component({
  selector: 'app-payment-info',
  templateUrl: './edit-payment-info.component.html',
  styleUrls: ['./edit-payment-info.component.scss']
})
export class EditPaymentInfoComponent implements OnInit {

  sessionId: string = "";
  clientId: string = "";
  mode: string = "";
  full_invoice_no: string = "";
  client_acct_id: string = "";
  acct_no: string = "";
  master_plan_intance_no: string = "";
  cardNo:string = "";
  securityNo:string="";
  
  constructor(private route: ActivatedRoute, private _paymentService: PaymentInfoService, @Inject(MAT_DIALOG_DATA) public data: any,
  public matDialogRef: MatDialogRef<EditPaymentInfoComponent>) { }

  ngOnInit(): void {
    if (this.data) {
      this.client_acct_id = this.data.client_acct_id;
      this.master_plan_intance_no = this.data.master_plan_id;
      this.getPaymentInitials();
    }
  }

  getPaymentInitials() {
    const reqObj = {
      client_acct_id: this.client_acct_id,
    }
    this._paymentService.getInitPaumentProcess(reqObj).subscribe((result) => {
      console.log(result);
      this.sessionId = result.inSessionID;
      this.clientId = result.client_no;
      this.mode = result.mode;
    });
  }

  onSubmit(form: any, e: any): void {
    //Note that I added 'e' and calling the event target's .submit()
    e.target.submit();
  }

  onCancel(){
    this.matDialogRef.close();
  }
}
